body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
  width: 100%;
  background: grey;
  margin: 15px;
}

body {
  background: #dfdfdf !important;
}

.textArea__component {
  width: 40%;
  float: left;
  padding: 18px;
}

.markdowned__component {
  width: 60%;
  float: right;
  padding: 18px;
}
.textarea>textarea {
	padding: 10px 25px;
}
pre{
	border: 1px solid black;
	padding: 10px;
}

th, td {
    padding: 6px 13px;
    border: 1px solid #dfe2e5;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

.markdowned__component>.markdown {
	padding-right: 50px;
}

